/*grid-template-columnsê°€ ì ìš©ë˜ì§€ ì•ŠëŠ” ê²ƒì€ ë‚´ë¶€ itemë“¤ì˜ ë„ˆë¹„ê°€ gridì˜ ì „ì²´ ë„ˆë¹„ë¥¼ ì´ˆê³¼í•˜ê¸° ë•Œë¬¸ì´ë¼ê³  ìƒê° */
.forms {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(60px, auto)) repeat(auto-fit, minmax(220px, auto));
  /* ë°˜ì‘í˜• ì—´ í¬ê¸° ì„¤ì • */
  grid-template-rows: minmax(148px, auto) minmax(148px, auto) minmax(112px, auto);
  /* í–‰ í¬ê¸° ì„¤ì • */
  max-height: 100vh;
  /* ë·°í¬íŠ¸ ë†’ì´ë¡œ ìµœëŒ€ ë†’ì´ ì œí•œ ì„¤ì • */

  border-bottom: 0.5px solid #999;
}

/* grid ë ˆì´ì•„ì›ƒì´ ì œëŒ€ë¡œ ì ìš©ë˜ì§€ ì•Šì€ê±´ itemsì˜ ë‚´ìš©ë“¤ì˜ í¬ê¸°ê°€ grid í‹€ì˜ í¬ê¸°ë¥¼ ë²—ì–´ë‚˜ì„œ ê·¸ëŸ°ê²Œ ì•„ë‹ê¹Œ ë¼ëŠ” ìƒê° */
.title {
  /* ì§€ì—­ */
  color: #666;
  text-align: center;
  font-family: Pretendard;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  /* 150% */
}

.required {
  font-size: 8px;
  color: red;
  margin-top: 0;
  margin-left: 8px;
}

.titles:nth-child(1) {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  display: grid;
  place-content: center center;
  height: 100%;
  width: 100%;
  border-bottom: 0.5px solid #999;
}

.titles:nth-child(3) {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  display: grid;
  place-content: center center;
  height: 100%;
  width: 100%;
  border-bottom: 0.5px solid #999;
}

.titles:nth-child(5) {
  grid-column: 1 / 2;
  grid-row: 3 / 4;
  display: grid;
  place-content: center center;
  height: 100%;
  width: 100%;
  border-bottom: 0.5px solid #999;
}

.datas:nth-child(2) {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  height: 100%;
  width: 100%;
  border-left: 0.5px solid #999;
  border-bottom: 0.5px solid #999;

  display: grid;
  align-items: center;
}

.datas:nth-child(4) {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  height: 100%;
  width: 100%;
  border-left: 0.5px solid #999;
  border-bottom: 0.5px solid #999;

  display: grid;
  align-items: center;
}

.datas:nth-child(6) {
